import { Link } from 'react-router-dom'
import avatar from '../assets/images/ray.jpg'
import avatar_webp from '../assets/images/ray.webp'
import { detectAnyAdblocker } from 'just-detect-adblock'
import { Section } from '../components/container'
import '../css/header.css'
import { useEffect, useState } from 'react'
import let_us_trace_gif from '../assets/images/let_us_trace.gif'
export default function Header(props) {
    const [adBlockStat, setAdBlockStat] = useState(false)
    const [gtagBlockStat, setgtagBlockStat] = useState(false)
    useEffect(() => {
        setTimeout(() => {
            try {
                const result = window.dataLayer.push('www');
                // console.log(result)
                setgtagBlockStat(result !== true);
                if (result !== true) {
                    document.body.classList.add("warning")
                }
            } catch (e) { console.log(e) }
            detectAnyAdblocker().then((stat) => {
                setAdBlockStat(stat);
                if (stat) {
                    document.body.classList.add("warning")
                }
            })
        }, 3000)
    }, [])
    let AdBlockWarning = null
    let gtagBlockWarning = null
    if (adBlockStat) {
        AdBlockWarning = (
            <Section id="adblock_warning">
                <h2 style={{ color: "red" }}>什麼，你居然在使用 AdBlock⁉️</h2>
                <div style={{ color: "yellow", fontWeight: 600 }}>你大老遠跑來這邊，就是為了要用 AdBlock⁉️</div>
            </Section>
        )
    }
    if (gtagBlockStat) {
        gtagBlockWarning =
            <Section id="gtag_warning">
                <h2 style={{ color: "red" }}>什麼，你居然禁止 GTag 追蹤⁉️</h2>
                <div style={{ color: "yellow", fontWeight: 600 }}>
                    聽話，讓我看看！
                    <img style={
                        {
                            maxHeight: "10rem",
                            maxWidth: '50%',
                            marginTop: ".5rem",
                            // boxShadow: "3px 3px 1px blue, 7px 7px 1px purple",
                            animation: "videoAni 1s linear 0s infinite forwards",
                            display: "block"
                        }}
                        src={let_us_trace_gif}
                    />
                </div>
            </Section>
    }

    return (
        <div>
            <div className={"section header " + (props.expand ? "expand" : "")}>
                <div className="avatar_wrapper">
                    <div className="avatar">
                        <picture>
                            <source srcSet={avatar_webp} />
                            <img src={avatar} />
                        </picture>
                    </div>
                </div>
                <div className="title_box">
                    <h1 className="text_texture crystal">Ray's Another World</h1>
                    <h3 className="text_texture crystal">I'm A Programmer, I Have No Life.</h3>
                </div>
                <nav className="nav">
                    <ul className="text_texture crystal">
                        <li><Link to="/">首頁</Link></li>
                        <li><Link to="/about">關於</Link></li>
                        <li><Link to="/portfolio">作品叢</Link></li>
                    </ul>
                </nav>
            </div>
            { AdBlockWarning}
            { gtagBlockWarning}
        </div>
    )
}